<template>
  <div id="body">
    <form action="/#">
      <div class="container shadow p-3 bg-white rounded my-5 mb-2">
          <h2>Register</h2>
          <hr>
          <label for="email"><b>Email</b></label>
          <input type="text" placeholder="Enter Email" name="email" id="email" required>

          <label for="psw"><b>Password</b></label>
          <input type="password" placeholder="Enter Password" name="psw" id="psw" required>

          <label for="psw-repeat"><b>Repeat Password</b></label>
          <input type="password" placeholder="Repeat Password" name="psw-repeat" id="psw-repeat" required>
          <hr>
          <p>By creating an account you agree to our <a href="#">Terms & Privacy</a>.</p>

          <button type="submit" class="registerbtn">Register</button>
        </div>
      <div class="container signin shadow p-3 mb-3  rounded">
      <p>Already have an account? <router-link to="/"><button class="btn btn-secondary">Sign in</button></router-link></p>
      </div>
    </form>
  </div>
</template>

<script>

export default {
  name: 'RegisterPage',
  components: {
  },
  methods: {

  }
}
</script>

<style scoped>
#body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f3f5f7;
}

* {
  box-sizing: border-box;
}

/* Add padding to container */
.container {
  padding: 16px;
  background-color: white;
  max-width: 700px;
}

.sp-signin {
  background: #66666647;
  border-radius: 5px;
  padding: 3px;
}
/* Full-width input fields */
input[type=text], input[type=password] {
  width: 100%;
  padding: 9px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  border-radius: 5px;
  background: #f1f1f1;
  font-size: 1rem;
}

input[type=text]:focus, input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

label {
  display: flex;
}

.btn-secondary {
  padding: 1px 10px !important;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/* Set a style for the submit button */
.registerbtn {
  background-color: #4CAF50;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
}

.registerbtn:hover {
  opacity: 1;
}

/* Add a blue text color to links */
a {
  color: dodgerblue;
}

/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}
</style>



